<script>
export default {
    name:'leftSideNav',
    data(){
        return {
            isOnMic: false,
            isAudioVisualizerOn: false,
            audioLevel: 0,
            audioContext: null,
            analyser: null,
            microphone: null,
            isListening: false,
            recognition: null,
            transcribedText: ''
        }
    },
    mounted() {
        // 컴포넌트가 마운트되면 자동으로 마이크 감지 시작
        this.startAudioVisualization();
    },
    methods: {
        toggleAudioVisualizer() {
            this.isAudioVisualizerOn = !this.isAudioVisualizerOn;
            if (this.isAudioVisualizerOn) {
                this.startSpeechRecognition();
            } else {
                this.stopSpeechRecognition();
            }
        },
        async startAudioVisualization() {
            if (this.isListening) return; // 이미 감지 중이면 중복 실행 방지
            
            try {
                const stream = await navigator.mediaDevices.getUserMedia({ audio: true });
                this.audioContext = new (window.AudioContext || window.webkitAudioContext)();
                this.analyser = this.audioContext.createAnalyser();
                this.microphone = this.audioContext.createMediaStreamSource(stream);
                
                this.analyser.fftSize = 256;
                this.microphone.connect(this.analyser);
                
                this.isListening = true;
                this.updateVisualization();
            } catch (error) {
                console.error('마이크 접근 오류:', error);
                this.isListening = false;
            }
        },
        stopAudioVisualization() {
            if (this.audioContext) {
                this.audioContext.close();
                this.audioContext = null;
                this.analyser = null;
                this.microphone = null;
                this.audioLevel = 0;
                this.isListening = false;
            }
        },
        updateVisualization() {
            if (!this.analyser) return;

            const dataArray = new Uint8Array(this.analyser.frequencyBinCount);
            this.analyser.getByteFrequencyData(dataArray);

            // 전체 주파수 대역의 평균값 계산
            let sum = 0;
            for (let i = 0; i < dataArray.length; i++) {
                sum += dataArray[i];
            }
            // 더 민감하게 반응하도록 계수 조정
            this.audioLevel = Math.min(100, (sum / dataArray.length) * 0.6);

            requestAnimationFrame(this.updateVisualization);
        },
        getBarColor(level) {
            // 레벨에 따른 색상 변화
            const hue = 120 - (level * 1.2); // 녹색(120)에서 빨간색(0)으로
            return `hsl(${hue}, 100%, 50%)`;
        },
        async startSpeechRecognition() {
            try {
                // Web Speech API 지원 여부 확인
                if (!('webkitSpeechRecognition' in window) && !('SpeechRecognition' in window)) {
                    console.error('이 브라우저는 Web Speech API를 지원하지 않습니다.');
                    return;
                }
                const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
                this.recognition = new SpeechRecognition();
                this.recognition.lang = 'ko-KR';
                this.recognition.continuous = false; // 한 문장 단위 인식
                this.recognition.interimResults = false;
                this.recognition.onresult = (event) => {
                    const transcript = event.results[0][0].transcript;
                    this.transcribedText = transcript;
                    console.log('인식된 텍스트:', transcript);
                };
                this.recognition.onend = () => {
                    if (this.isListening && this.isAudioVisualizerOn) {
                        this.startSpeechRecognition(); // 자동 재시작
                    }
                };
                this.recognition.start();
                this.isListening = true;
            } catch (error) {
                console.error('마이크 접근 오류:', error);
            }
        },
        stopSpeechRecognition() {
            if (this.recognition) {
                this.isListening = false;
                this.recognition.stop();
            }
        }
    },
    beforeUnmount() {
        this.stopAudioVisualization();
    }
}
</script>
