<template>
    <div class="analysis-section">
        <div class="section-header">
            <h2>채팅 통계</h2>
            <div class="date-filter">
                <SelectLive :liveList="liveHistory" @selected="handleSelectedLive" />
                <select v-model="selectedStatsPeriod" @change="fetchChatStats">
                    <option value="hour">시</option>
                    <option value="day">일</option>
                    <option value="month">월</option>
                    <option value="year">연</option>
                    <option value="all">전체</option>
                </select>
            </div>
        </div>
        <div class="chat-history-summary">
            <div class="summary-card">
                <h3>총 채팅 수</h3>
                <div class="summary-value">{{ statsData[0].value }}</div>
            </div>
            <div class="summary-card positive">
                <h3>긍정적 채팅</h3>
                <div class="summary-value">{{ statsData[2].value }}</div>
                <div class="summary-percentage">{{ calculatePositivePercentage() }}%</div>
            </div>
            <div class="summary-card negative">
                <h3>부정적 채팅</h3>
                <div class="summary-value">{{ statsData[3].value }}</div>
                <div class="summary-percentage">{{ calculateNegativePercentage() }}%</div>
            </div>
            <div class="summary-card neutral">
                <h3>중립적 채팅</h3>
                <div class="summary-value">{{ calculateNeutralCount() }}</div>
                <div class="summary-percentage">{{ calculateNeutralPercentage() }}%</div>
            </div>
        </div>
    </div>
</template>

<script>
import axiosInstance from '@/axios_config/axiosConfig';
import SelectLive from '../../common/selectLive.vue';
export default {
    name: 'ChatStats',
    components: {
        SelectLive
    },
    props: {
        channelId: {
            type: String,
            required: true
        },
        liveHistory: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
            statsData: [
                { label: '총채팅수', value: '12,846' },
                { label: '평균채팅비율', value: '23.4%' },
                { label: '긍정적채팅', value: '68%' },
                { label: '부정적채팅', value: '8%' },
            ],
            selectedStatsPeriod: 'hour',
            selectedLiveId: '',
        }
    },
    mounted() {
        this.fetchChatStats();
    },
    methods: {
        async fetchChatStats() {
            try {
                const response = await axiosInstance.get(`/chat/analytics/channel_chat_summary`, {
                    params: {
                        channel_id: this.channelId,
                        live_key: this.selectedLiveId,
                        period: this.selectedStatsPeriod
                    }
                });
                this.statsData = response.data.content.stats;
            } catch (error) {
                console.error('채팅 통계 로딩 실패:', error);
            }
        },
        calculatePositivePercentage() {
            const total = parseInt(this.statsData[0].value.replace(/,/g, ''));
            const positive = parseInt(this.statsData[2].value.replace(/,/g, ''));
            return total ? ((positive / total) * 100).toFixed(1) : '0.0';
        },
        calculateNegativePercentage() {
            const total = parseInt(this.statsData[0].value.replace(/,/g, ''));
            const negative = parseInt(this.statsData[3].value.replace(/,/g, ''));
            return total ? ((negative / total) * 100).toFixed(1) : '0.0';
        },
        calculateNeutralCount() {
            const total = parseInt(this.statsData[0].value.replace(/,/g, ''));
            const positive = parseInt(this.statsData[2].value.replace(/,/g, ''));
            const negative = parseInt(this.statsData[3].value.replace(/,/g, ''));
            return (total - positive - negative).toLocaleString();
        },
        calculateNeutralPercentage() {
            const total = parseInt(this.statsData[0].value.replace(/,/g, ''));
            const positive = parseInt(this.statsData[2].value.replace(/,/g, ''));
            const negative = parseInt(this.statsData[3].value.replace(/,/g, ''));
            const neutral = total - positive - negative;
            return total ? ((neutral / total) * 100).toFixed(1) : '0.0';
        },
        handleSelectedLive(selectedId) {
            if (selectedId) {
                this.selectedLiveId = selectedId;
                this.fetchChatStats();
            }
        },
    }
}
</script>

<style scoped>
@import '@/assets/chat-analysis-common.css';
</style> 