<template>
  <div class="highlight-analysis">
    <div class="left-section">
      <div class="time-selector">
        <SelectLive :liveList="liveHistory" @selected="handleSelectedLive" />
        <select v-model="period" @change="fetchHighlightData">
          <option value="10min">10분</option>
          <option value="30min">30분</option>
          <option value="hour">시</option>
          <option value="day">일</option>
          <option value="month">월</option>
          <option value="year">연</option>
          <option value="all">전체</option>
        </select>
      </div>
      <div class="graph-container" ref="graphContainer"></div>
    </div>
    <div class="right-section">
      <div class="highlight-detail">
        <h3>하이라이트 상세 정보</h3>
        <template v-if="selectedHighlight">
          <div>시간: {{ formatTime(selectedHighlight.time_unit) }}</div>
          <div>하이라이트 발생: {{ selectedHighlight.highlight_occurrences }}회</div>
          <div>평균 채팅 수: {{ selectedHighlight.avg_chat_count?.toFixed(2) }}</div>
          <div>평균 upper_band: {{ selectedHighlight.avg_upper_band?.toFixed(2) }}</div>
        </template>
        <template v-else>
          <p>그래프의 점을 클릭하면 상세 정보가 표시됩니다.</p>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
import axiosInstance from '@/axios_config/axiosConfig';
import * as echarts from 'echarts';
import SelectLive from '../../common/selectLive.vue';

export default {
  name: 'HighlightAnalysis',
  components: {
    SelectLive
  },
  props: {
    liveHistory: {
      type: Array,
      required: true
    }
  },
  data() {
    return {
      period: 'hour',
      chart: null,
      selectedHighlight: null,
      selectedLiveId: null
    }
  },
  methods: {
    async handleSelectedLive(selectedId) {
      this.selectedLiveId = selectedId;
      if (this.selectedLiveId !== null) {
        this.fetchHighlightData();
      }
    },
    async fetchHighlightData() {

      try {
        // 실제 API 호출은 주석 처리
        const response = await axiosInstance.get(`/chzzk/hightlights/summary/${this.$route.params.channelId}`, {
          params: {
            live_key: this.selectedLiveId,
            period: this.period
          }
        });
        if (response.data.status_code === 200) {
          this.highlightData = await response.data.content.highlights;
        } else {
          console.error('하이라이트 데이터 로딩 실패:', response.data.detail);
        }
        this.updateGraph();
      } catch (error) {
        console.error('하이라이트 데이터 로딩 실패:', error);
      }
    },
    updateGraph() {
      if (!this.highlightData || !this.highlightData.length) {
        if (this.chart) {
          this.chart.clear();
        }
        return;
      }

      if (!this.chart) {
        this.chart = echarts.init(this.$refs.graphContainer);
      }

      const data = this.highlightData.map(item => {
        let dateStr = item._id;
        if (/^\d{4}-\d{2}-\d{2} \d{2}$/.test(dateStr)) {
          dateStr = dateStr.replace(' ', 'T') + ':00:00';
        } else if (/^\d{4}-\d{2}-\d{2} \d{2}:\d{2}$/.test(dateStr)) {
          dateStr = dateStr.replace(' ', 'T') + ':00';
        } else {
          dateStr = dateStr.replace(' ', 'T');
        }
        return {
          value: [new Date(dateStr), item.count],
          itemStyle: { color: '#4CAF50' },
          time_unit: item._id,
          highlight_occurrences: item.count,
          avg_upper_band: item.avg_upper_band,
          avg_chat_count: item.avg_chat_count
        };
      });

      let maxYValue = 0;
      if (data.length > 0) {
        maxYValue = Math.max(...data.map(item => item.highlight_occurrences));
      }
      const yAxisMax = maxYValue > 0 ? Math.ceil(maxYValue * 1.1) : 10;

      const option = {
        backgroundColor: '#2a2a2a',
        title: {
          text: '시간대별 하이라이트 발생 빈도',
          textStyle: {
            color: '#ffffff'
          }
        },
        tooltip: {
          trigger: 'item',
          backgroundColor: 'rgba(50, 50, 50, 0.9)',
          borderColor: '#555',
          textStyle: {
            color: '#fff'
          },
          formatter: function(params) {
            let tooltipText = `시간: ${new Date(params.data.time_unit).toLocaleString()}<br/>
                               하이라이트 발생: ${params.data.highlight_occurrences}회`;
            if (params.data.avg_chat_count !== undefined) {
              tooltipText += `<br/>평균 채팅 수: ${params.data.avg_chat_count.toFixed(2)}`;
            }
            if (params.data.avg_upper_band !== undefined) {
              tooltipText += `<br/>평균 upper_band: ${params.data.avg_upper_band.toFixed(2)}`;
            }
            return tooltipText;
          }
        },
        grid: {
          left: '5%',
          right: '5%',
          top: '60',
          bottom: '60',
          containLabel: true
        },
        xAxis: {
          type: 'time',
          splitLine: {
            show: false
          },
          axisLine: {
            lineStyle: {
              color: '#555'
            }
          },
          axisLabel: {
            color: '#fff',
            formatter: function (value) {
                const date = new Date(value);
                return `${date.getHours()}:${String(date.getMinutes()).padStart(2, '0')}`;
            },
            interval: 'auto',
            hideOverlap: true
          },
          minInterval: 1000 * 60 * 60 // 1시간 간격
        },
        yAxis: {
          type: 'value',
          name: '하이라이트 발생 횟수',
          nameTextStyle: {
              color: '#ccc',
              padding: [0, 0, 0, 50]
          },
          min: 0,
          max: yAxisMax,
          splitLine: {
            show: true,
            lineStyle: {
              type: 'dashed',
              color: '#555'
            }
          },
          axisLine: {
            lineStyle: {
              color: '#555'
            }
          },
          axisLabel: {
            color: '#fff',
            formatter: (value) => value.toFixed(0)
          }
        },
        series: [
          {
            type: 'scatter',
            name: '하이라이트 발생',
            data: data,
            symbolSize: 12,
            itemStyle: {
              borderColor: '#fff',
              borderWidth: 1
            },
            emphasis: {
              itemStyle: {
                borderColor: '#fff',
                borderWidth: 2,
                shadowBlur: 10,
                shadowColor: 'rgba(255, 255, 255, 0.5)'
              }
            }
          },
          {
            type: 'line',
            name: '추세선',
            data: data.map(item => item.value), // [Date, count] 배열
            symbol: 'none',
            lineStyle: {
              color: '#4CAF50',
              width: 2,
              opacity: 0.7
            }
          }
        ]
      };

      this.chart.setOption(option);

      this.chart.on('click', (params) => {
        if (params.seriesType === 'scatter' && params.data) {
          this.selectedHighlight = params.data;
        }
      });
    },
    formatTime(timestamp) {
      let dateStr = timestamp;
      if (/^\d{4}-\d{2}-\d{2} \d{2}$/.test(dateStr)) {
        dateStr = dateStr.replace(' ', 'T') + ':00:00';
      } else if (/^\d{4}-\d{2}-\d{2} \d{2}:\d{2}$/.test(dateStr)) {
        dateStr = dateStr.replace(' ', 'T') + ':00';
      } else {
        dateStr = dateStr.replace(' ', 'T');
      }
      const date = new Date(dateStr);
      return date.toLocaleString('ko-KR', {
        year: 'numeric',
        month: '2-digit',
        day: '2-digit',
        hour: '2-digit',
        minute: '2-digit',
        second: '2-digit'
      });
    }
  },
  mounted() {
    // this.fetchHighlightData();
    window.addEventListener('resize', () => {
      if (this.chart) {
        this.chart.resize();
      }
    });
  },
  beforeUnmount() {
    if (this.chart) {
      this.chart.dispose();
    }
    window.removeEventListener('resize', this.updateGraph);
  }
}
</script>

